html, body {
  box-sizing: border-box;
  background: #050505;
  overflow: hidden;
  color: #fafafa; }

.no-btn {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit; }

.layout {
  background: #050505; }

.target {
  position: absolute;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 0.5;
  pointer-events: none; }

.target-icon {
  position: absolute;
  width: 40px; }
